<template>
  <div>
    <Header name="我的积分"></Header>
    <div style="min-height:740px;background:#f6f6f6;position: relative;">
      <div class="img">
        <img :src="require('@/assets/image/otherbg.png')" alt="" width="100%" height="180px"/>
      </div>
      <div class="jifen">
        <div class="top">
          <div>
            <p class="head">{{current}}</p>
            <p>积分</p>
          </div>
          <div>
            <p class="head">{{memberLevel}}</p>
            <p>当前等级</p>
          </div>
        </div>
        <div class="bottom">
          <van-tabs
            v-model="active"
            color="#fd2744"
            line-width="26px"
            line-height="4px"
          >
            <van-tab title="积分明细">
              <!-- 这里写v-for -->
              <div class="detailList" v-for="item in data" :key="item.id">
                <div class="left">
                  <div class="type">{{item.operateNote}} {{item.operateNote === '购买商品' || item.operateNote === '积分购买' ? ' - 订单号：' + item.sourceCode : ''}}</div>
                  <div class="time">{{item.createTime}}</div>
                </div>
                <div class="right">
                  <div class="add" v-if="item.changeType === 0">+{{item.changeCount}}</div>
                  <div class="reduce" v-if="item.changeType === 1">-{{item.changeCount}}</div>
                  <div class="all">{{item.operateNote}}</div>
                  <!-- {{item.sourceType | getName(item.sourceType)}} -->
                </div>
              </div>
              <div style="height: 10px"></div>
            </van-tab>
            <van-tab title="积分说明">
              <div class="font">
                <p>一、积分说明</p>
                &emsp;&emsp;<span style="color:red">*&nbsp;</span><span style="color:black;font-weight:bold">积分=累积购物金额</span><br />
                &emsp;&emsp;<span style="color:red">*&nbsp;</span><span style="color:black;font-weight:bold">1元=1积分，累积购物金额越高，积分越高，对应的用户等级也会升级。</span><br />
                &emsp;&emsp;1&nbsp;&nbsp;随着您购买商品金额的累加，您的账户的等级积分将对应增加，一旦符合晋级条件，系统将自动升级您的会员级别，并扣除所需积分。<br />
                &emsp;&emsp;2&nbsp;&nbsp;衡磊电子商城会员级别分为：<br />
                &emsp;&emsp;&emsp;(1)&nbsp;&nbsp;个人会员：普通会员、银卡会员、金卡会员、钻石会员；<br />
                &emsp;&emsp;&emsp;(2)&nbsp;&nbsp;企业会员：普通会员、专业会员、三星会员、四星会员、五星会员、六星会员、七星会员；<br />
                <!-- &emsp;&emsp;&emsp;(3)&nbsp;&nbsp;政府、院校会员：普通会员、VIP会员、至尊VIP会员；<br />
                &emsp;&emsp;不同会员等级享受不同购物优惠！<br /> -->
                会员等级：<br />
                <table>
                  <thead>
                  <tr>
                    <th style="vertical-align: middle">会员类别</th>
                    <th style="vertical-align: middle">等级</th>
                    <th style="vertical-align: middle">所需积分</th>
                    <th style="vertical-align: middle">会员有效期</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                    <th style="vertical-align: middle" rowspan="4">个人会员</th>
                    <th style="vertical-align: middle">普通会员</th>
                    <th style="vertical-align: middle">0</th>
                    <th style="vertical-align: middle">永久有效</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">银卡会员</th>
                    <th style="vertical-align: middle">5万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除2.5万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">金卡会员</th>
                    <th style="vertical-align: middle">10万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除5万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">钻石会员</th>
                    <th style="vertical-align: middle">20万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除10万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle" rowspan="7">企业会员</th>
                    <th style="vertical-align: middle">普通会员</th>
                    <th style="vertical-align: middle">0</th>
                    <th style="vertical-align: middle">永久有效</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">专业会员</th>
                    <th style="vertical-align: middle">资质审核</th>
                    <th style="vertical-align: middle">永久有效</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">三星会员</th>
                    <th style="vertical-align: middle">50万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除25万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">四星会员</th>
                    <th style="vertical-align: middle">100万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除50万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">五星会员</th>
                    <th style="vertical-align: middle">200万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除100万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">六星会员</th>
                    <th style="vertical-align: middle">500万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除250万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">七星会员</th>
                    <th style="vertical-align: middle">1000万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除500万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <!-- <tr>
                    <th style="vertical-align: middle" rowspan="3">政府、院校会员</th>
                    <th style="vertical-align: middle">普通会员</th>
                    <th style="vertical-align: middle">0</th>
                    <th style="vertical-align: middle">永久有效</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">VIP会员</th>
                    <th style="vertical-align: middle">10万</th>
                    <th style="vertical-align: middle">会员有效期1年，1年后扣除5万积分，根据剩余积分重新计算级别</th>
                  </tr>
                  <tr>
                    <th style="vertical-align: middle">至尊VIP会员</th>
                    <th style="vertical-align: middle">资质审核</th>
                    <th style="vertical-align: middle">永久有效</th>
                  </tr> -->
                  </tbody>
                </table>
                <p>二、积分如何获得？</p>
                &emsp;&emsp;(1)&nbsp;&nbsp;消费即可获得积分，消费一元获得一积分；
                <p>三、积分有什么用处？</p>
                &emsp;&emsp;(1)&nbsp;&nbsp;.部分等级会员可享受商品折扣，以及获取代金券，用户等级越高代金券比例越高；<br/>
                &emsp;&emsp;注：<br/>
                &emsp;&emsp;（1）个人会员：银卡级以上会员享受商品折扣；<br/>
                &emsp;&emsp;（2）企业会员：专业会员和五星级以上会员享受商品折扣；<br/>
                &emsp;&emsp;（3）政府、院校会员：VIP级以上会员享受商品折扣；<br/>
                &emsp;&emsp;(1)&nbsp;&nbsp;.部分会员可享受使用积分兑换指定商品（积分兑换商品无法退款）；
                <p>四、积分累积方式及有效期</p>
                &emsp;&emsp;衡磊电子商城积分有效期截至当年年底，过期作废（部分会员会依据会员等级将积分自动转换为对应比例的代金券）。
                <p style="font-weight:bold;">&emsp;&emsp;<span style="color:red">*&nbsp;</span>如2023年1月1日起产生积分，失效日期为2023年12月31日。</p>
              </div>
            </van-tab>
          </van-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
export default {
  components :{
    Header
  },
  data() {
    return {
      active:0,
      total: 0,
      current: '',
      memberLevel: '',
      labelName: '',
      data: []
    }
  },
  filters:{
    getName: function (nl) {
        if (nl === 0) {
          return '购物';
        } else if (nl === 1) {
          return '管理员修改';
        } else if (nl === 2) {
          return '返代金券';
        } else if (nl === 3) {
          return '退货扣回';
        } else {
          return '-'
        }
      }
  },
  mounted(){
    this.memberIntegration()
  },
  methods: {
    memberIntegration () {
        let me = this
        api.memberIntegration({pageNum:1, pageSize: 100}).then(res => {
          if (res.code === 200) {
            me.data = res.data.page.list
            me.labelName = res.data.remark
            me.memberLevel = res.data.memberLevel
            me.current = res.data.current
            me.total = res.data.page.total
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      },
  },
};
</script>

<style lang="stylus" scoped>
.jifen {
  width: 94.6%;//355
  z-index: 999;
  margin: 0px 2.66%;
  position: absolute;
  top: 40px;

  .top {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-around;
    align-items center
    div {
      text-align: center;

      .head {
        font-size: 30px;
        font-weight: bold;
        color: #FFFFFF;
        letter-spacing: 1px;
      }

      p {
        font-size: 13px;
        color: #FFFFFF;
        margin-top 10px
      }
    }
  }

  .bottom {
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.05);
    border-radius: 12px;
    margin: 20px 0px
    overflow hidden

    .detailList {
      width: 91.5%;
      height: 74px;
      border-bottom: 1px solid #eee;
      margin: 10px 4.22%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .type {
        font-size: 15px;
        font-weight: bold;
        color: #000000;
      }

      .add {
        font-size: 16px;
        font-weight: bold;
        color: #FD2744;
      }

      .reduce {
        font-size: 16px;
        font-weight: bold;
        color: #000;
      }

      .time, .all {
        font-size: 12px;
        color: #999999;
        margin-top: 8px;
      }

      .right {
        text-align: right;
      }
    }

    .font {
      margin: 13px 4.22%;
      width: 91.5%;
      font-size: 13px;
      color: #666666;
      line-height: 26px;
      table{
        width: 100%;
        border: 1px solid #666
        tr{
          border: 1px solid #666
        }
        th{
          border: 1px solid #666
        }
      }

      p {
        color: #000;
        font-weight: bold;
      }
    }
  }
}
</style>


